	font-style: normal;
}

@font-face {
	font-family: 'Playfair Display Bold';
	src: local("Playfair Display Bold"), local("playfairdisplaybold"), url("../fonts/playfairdisplaybold.woff2") format("woff2"), url("../fonts/playfairdisplaybold.woff") format("woff");
	font-weight: normal;
	font-style: normal;
}

html {
	font-size: 10px;
	box-sizing: border-box;
	-webkit-text-size-adjust: 100%;
}

html, body {
	height: 100%;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	/*font-size: 100%;
    font: inherit;*/
	vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}

* {
	margin: 0;
	padding: 0;
	outline: none;
	line-height: inherit;
	min-width: 0;
	min-height: 0;
}

*,
*:before,
*:after {
	box-sizing: inherit;
}

ol, ul {
	list-style: none;
}

blockquote, q {
	quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}

.clearfix:after,
.clearfix:before {
	display: table;
	content: ' ';
}

.clearfix:after {
	clear: both;
}

code,
kbd,
samp {
	font-family: monospace, monospace;
	font-size: 1em;
}

body {
	font-weight: 400;
	font-size: 1.3rem;
	font-family: "Inter", Arial, Helvetica, sans-serif;
	color: #000;
	line-height: 1;
	position: relative;
	min-width: 320px;
	display: flex;
	flex-direction: column;
}

h2, h3, h4, h5, b, strong {
	font-weight: 700;
}

p {
	line-height: 1.55;
	margin-bottom: 20px;
}

p:last-child {
	margin-bottom: 0;
}

a {
	text-decoration: none;
}

img {
	max-width: 100%;
	height: auto;
	max-height: 100%;
}

.full-bg {
	background-repeat: no-repeat;
	background-position: center center;
	background-size: cover;
}

.full-width {
	max-width: 1920px;
	margin: 0 auto;
}

.all-wrapp {
	flex: 1 0 auto;
	position: relative;
	width: 100%;
}

.container {
	max-width: 1175px;
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
}

.row {
	display: flex;
	flex-wrap: wrap;
	margin-right: -15px;
	margin-left: -15px;
}

.col {
	flex-basis: 0;
	flex-grow: 1;
	padding-right: 15px;
	padding-left: 15px;
}

.hide {
	display: none;
}

.bg-1 {
	background: #fbfbfb;
}

.h1-title {
	font-weight: 700;
	font-size: 3rem;
	color: #333;
	display: block;
	margin-bottom: 25px;
}

.title {
	font-weight: 700;
	font-size: 3.6rem;
	font-family: 'Playfair Display Bold', serif;
	color: #BE2C37;
	display: block;
	line-height: 1;
	margin-bottom: 20px;
}

.btn {
	background: #c12236;
	font-weight: 600;
	font-size: 1.3rem;
	color: #fff;
	display: inline-block;
	height: 40px;
	padding: 0 30px;
	line-height: 40px;
	border-radius: 25px;
	border: none;
	cursor: pointer;
	transition: background 0.3s ease-in-out,border-color 0.3s ease-in-out,color 0.3s ease-in-out;
}

.btn:hover {
	background: #e00924;
}

.btn--2 {
	background: transparent;
	color: #be2c37;
	line-height: 39px;
	border: 1px solid #c12236;
}

.btn--2:hover {
	background: #c12236;
	color: #fff;
}

@media (max-width: 767px) {
	p {
		font-size: 1.2rem;
		line-height: 1.4;
	}
}

/*== begin header ==*/
.header .header-wrapp {
	background: #c12236;
	position: fixed;
	width: 100%;
	padding: 10px 0 12px;
	top: 0;
	left: 0;
	z-index: 10;
}

.header .header-wrapp.sticky {
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
	transition: all 0.5s ease;
}

.header .row {
	justify-content: space-between;
	align-items: center;
}

.header-logo {
	flex: 0 0 220px;
	max-width: 220px;
}

.logo {
	display: inline-block;
	line-height: 0;
}

.header-second {
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

/* ==== MAIN MENU ===== */
.main-menu {
	position: relative;
	width: 100%;
	display: flex;
	justify-content: flex-end;
}

.main-menu li {
	position: relative;
}

.main-menu li a {
	color: #fff;
	display: inline-block;
	transition: all 0.3s ease;
}

.main-menu > li {
	display: block;
	padding: 5px 0;
	margin-left: 30px;
}

.main-menu > li:first-child {
	margin-left: 0;
}

.main-menu > li > a {
	font-weight: 700;
	font-size: 1.6rem;
	position: relative;
	text-transform: uppercase;
	display: inline-block;
	padding: 2px 0;
	transition: all 0.3s ease;
}

.main-menu > li > a:after {
	background: #fff;
	position: absolute;
	display: block;
	content: "";
	width: 100%;
	height: 1px;
	bottom: -2px;
	left: 0;
	transform: scaleX(0);
	transform-origin: center center;
	transition: all 0.3s ease;
}

.main-menu > li > a:hover:after {
	transform: scaleX(1);
}

.main-menu .main-menu__sub {
	padding-right: 10px;
}

.main-menu .main-menu__sub > a::after {
	display: none;
}

.main-menu .main-menu__sub::before {
	position: absolute;
	display: block;
	content: "";
	background-image: url(../img/sprite.png);
	background-position: -19px -25px;
	width: 11px;
	height: 7px;
	top: 11px;
	right: -8px;
	transition: all 0.3s ease;
}

.main-menu .main-menu__sub ul {
	background: #c12236;
	position: absolute;
	width: 250px;
	height: 0;
	top: 100%;
	left: 0;
	padding: 0 10px 0 15px;
	transform: scaleY(0);
	transition: all 0.3s ease;
	transform-origin: left top;
	box-shadow: 0 3px 7px rgba(0, 0, 0, 0.25);
	z-index: 3;
}

.main-menu .main-menu__sub ul a {
	padding: 4px 0;
}

.main-menu .main-menu__sub ul a:hover {
	color: #dfdfdf;
}

.main-menu .main-menu__sub:hover::before {
	transform: rotate(-180deg);
}

.main-menu .main-menu__sub:hover ul {
	height: auto;
	transform: scale(1);
	padding: 15px 10px 15px 15px;
}

/* === button menu open === */
.button-menu {
	background-color: transparent;
	position: relative;
	display: block;
	width: 47px;
	line-height: 34px;
	transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
	backface-visibility: hidden;
	text-align: center;
	padding: 0;
	margin-top: -2px;
}

.button-menu:focus, .button-menu:hover {
	background-color: transparent;
}

.button-menu .menu-fader {
	/*font-weight: 700;
         font-size:  1.4rem;
         color: #0d2f5c;
         text-transform: uppercase;
         margin-right: 14px;*/
	display: none;
}

.button-menu span {
	background-color: #fff;
	position: relative;
	display: inline-block;
	width: 100%;
	height: 5px;
	backface-visibility: hidden;
}

.button-menu span:before, .button-menu span:after {
	background-color: inherit;
	position: absolute;
	left: 0;
	width: 100%;
	height: 100%;
	content: '';
	transition: all 0.4s cubic-bezier(0.23, 1, 0.32, 1);
	backface-visibility: hidden;
}

.button-menu span:before {
	transform: translate3d(0, -210%, 0);
}

.button-menu span:after {
	transform: translate3d(0, 210%, 0);
}

.button-menu.active span {
	transform: rotate(45deg);
}

.button-menu.active span:before {
	transform: rotate(180deg);
}

.button-menu.active span:after {
	transform: rotate(270deg);
}

/* === button menu open === */
.close-menu {
	display: none;
}

.button-search {
	background-color: transparent;
	position: relative;
	display: inline-block;
	width: 28px;
	height: 24px;
	border: none;
	cursor: pointer;
	margin-left: 48px;
	transition: all 0.3s ease;
}

.button-search::before {
	position: absolute;
	display: block;
	content: "";
	background-image: url(../img/sprite.png);
	background-position: -25px 0px;
	width: 24px;
	height: 24px;
	top: 0;
}

.button-search:hover {
	opacity: 0.8;
}

.header-search {
	background: #c12236;
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	z-index: -2;
	opacity: 0;
	transition: all 0.7s ease;
}

.header-search.active {
	opacity: 1;
	z-index: 2;
}

.form-search {
	display: flex;
	width: 100%;
	height: 36px;
	margin-top: 17px;
}

.form-search .form-search__input, .form-search .form-search__button, .form-search .form-search__cancel {
	background: #fff;
	height: 100%;
	border: none;
	border-radius: 5px;
}

.form-search .form-search__input {
	flex: 1 0 auto;
	max-width: 100%;
	padding: 0 10px 0 15px;
}

.form-search .form-search__button, .form-search .form-search__cancel {
	position: relative;
	width: 40px;
	margin-left: 15px;
	cursor: pointer;
	transition: background 0.3s ease-in-out,border-color 0.3s ease-in-out,color 0.3s ease-in-out;
}

.form-search .form-search__button::after, .form-search .form-search__cancel::after {
	position: absolute;
	display: block;
	content: "";
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
}

.form-search .form-search__button::after {
	background-image: url(../img/sprite.png);
	background-position: 0px 0px;
	width: 24px;
	height: 24px;
}

.form-search .form-search__button:hover {
	background: rgba(255, 255, 255, 0.85);
}

.form-search .form-search__cancel {
	background: #000;
}

.form-search .form-search__cancel::after {
	background-image: url(../img/sprite.png);
	background-position: 0px -25px;
	width: 18px;
	height: 18px;
}

.form-search .form-search__cancel:hover {
	background: rgba(0, 0, 0, 0.5);
}

@media (max-width: 1100px) {
	.main-menu > li > a {
		font-size: 1.4rem;
	}
}

@media (max-width: 991px) {
	.main-menu > li {
		margin-left: 20px;
	}
	.main-menu > li > a {
		font-size: 1.2rem;
	}
	.main-menu .main-menu__sub::before {
		top: 9px;
	}
	.button-search {
		margin-left: 32px;
	}
}

@media (max-width: 860px) {
	.main-menu > li {
		margin-left: 15px;
	}
	.main-menu > li > a {
		font-size: 1.1rem;
	}
}

@media (max-width: 767px) {
	.header-button {
		display: block;
	}
	.box-main-menu {
		background: rgba(193, 34, 54, 0.9);
		position: fixed;
		top: 0;
		left: 0;
		padding: 40px 0 20px;
		margin: 0;
		width: 280px;
		height: 100%;
		text-align: left;
		z-index: 999;
		transform: translateX(-550px);
		transition: .7s;
		box-shadow: 2px 0 6px rgba(0, 0, 0, 0.35);
	}
	.box-main-menu.open {
		overflow: auto;
		transform: translateX(0);
	}
	.main-menu {
		display: block;
		padding: 0 15px;
	}
	.main-menu > li {
		text-align: left;
		padding-right: 20px;
		margin-left: 0;
		border-bottom: 1px dotted #ccc;
	}
	.main-menu > li > a {
		font-size: 1.6rem;
		padding: 8px 0;
		line-height: 1.1em;
	}
	.main-menu > li > a:after {
		display: none;
	}
	.main-menu .main-menu__sub {
		padding-right: 20px;
	}
	.main-menu .main-menu__sub ul {
		background: transparent;
		position: relative;
		top: auto;
		left: auto;
		box-shadow: none;
	}
	.main-menu .main-menu__sub:hover ul {
		padding-top: 0;
	}
	.main-menu .main-menu__sub::before {
		top: 18px;
		right: 0;
	}
	.close-menu {
		color: #fafafa;
		position: relative;
		display: block;
		font-weight: 300;
		font-size: 26px;
		position: absolute;
		right: 14px;
		top: 10px;
	}
	.close-menu::after {
		display: inline-block;
		content: '';
		background-image: url(../img/sprite.png);
		background-position: 0px -25px;
		width: 18px;
		height: 18px;
	}
}

@media (max-width: 420px) {
	.button-search {
		margin-left: 18px;
	}
	.main-menu > li > a {
		font-size: 1.4rem;
	}
	.form-search .form-search__button, .form-search .form-search__cancel {
		margin-left: 5px;
	}
}

@media (max-width: 355px) {
	.header .header-wrapp {
		padding-top: 11px;
	}
	.header-logo {
		flex: 0 0 170px;
		max-width: 170px;
	}
	.header-second {
		padding-left: 0;
	}
	.form-search {
		margin-top: 12px;
	}
}

/*== end header ==*/
/*== begin section premier ==*/
.section-premier {
	background-image: url("../img/bg/banner_1.jpg");
	padding: 132px 0 30px;
	overflow: hidden;
}

.section-premier .section-premier__one {
	flex: 0 0 303px;
	max-width: 303px;
}

.section-premier .h1-title {
	margin-bottom: 18px;
}

.slider-product {
	width: 100%;
}

.slider-product .slick-list {
	overflow: visible;
}

.slider-product .slick-track {
	display: flex;
}

.slider-product .slick-slide {
	flex: 0 0 auto;
	height: auto;
}

.slider-product .slider-product__item {
	background: #fff;
	display: flex;
	padding: 40px 40px 45px 60px;
	box-shadow: 0 3px 30px rgba(0, 0, 0, 0.7);
}

.slider-product .slider-product__img, .slider-product .slider-product__text {
	flex-basis: 0;
	flex-grow: 1;
	max-width: 100%;
}

.slider-product .slider-product__img {
	padding-right: 40px;
}

.slider-product .slider-product__text {
	flex: 0 0 260px;
	max-width: 260px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding-top: 22px;
}

.slider-product .slider-product__title {
	font-weight: normal;
	font-size: 2.4rem;
	font-family: 'Playfair Display Bold', serif;
	color: #333;
	display: block;
	line-height: 1.1;
}

.slider-product .slider-product__desc {
	font-size: 1.3rem;
	color: #333;
	display: block;
	line-height: 1.5;
	margin-top: 18px;
}

.slider-product .slider-product__footer {
	margin-top: 20px;
}

.slider-product .slick-dots {
	display: flex;
	justify-content: center;
	margin-top: 33px;
}

.slider-product .slick-dots li {
	margin: 0 5px;
}

.slider-product .slick-dots button {
	background: #fff;
	font-size: 0;
	width: 12px;
	height: 12px;
	opacity: 0.5;
	border: none;
	border-radius: 50%;
	cursor: pointer;
	transition: all 0.3s ease;
}

.slider-product .slick-dots button:hover {
	opacity: 1;
}

.slider-product .slick-dots .slick-active button {
	opacity: 1;
	cursor: default;
}

.product-nav .slick-list {
	padding: 0 !important;
}

.product-nav .slick-track {
	width: 100% !important;
	transform: translate(0) !important;
}

.product-nav .slick-slide {
	float: none;
}

.product-nav .product-nav__item {
	display: block;
	width: 100% !important;
}

.product-nav .product-nav__btn {
	font-weight: 700;
	font-size: 1.3rem;
	color: #333;
	display: inline-flex;
	justify-content: center;
	align-items: center;
	min-height: 40px;
	padding: 5px 20px;
	line-height: 1.1;
	border-radius: 25px;
	cursor: pointer;
