.tabs li.active a::before {
	background-image: url(../img/sprite.png);
	background-position: -50px 0px;
	width: 7px;
	height: 11px;
}

.tabs li a {
	font-weight: 600;
	font-size: 1.4rem;
	color: #333;
	position: relative;
	display: inline-block;
	line-height: 1.2;
	padding-left: 22px;
}

.tabs li a::before {
	position: absolute;
	display: block;
	content: "";
	background-image: url(../img/sprite.png);
	background-position: -50px -12px;
	width: 7px;
	height: 11px;
	top: 3px;
	left: 0;
}

.tabs li a:hover {
	color: #be2c37;
}

.tab-content {
	position: relative;
	height: 0;
	opacity: 0;
	transform: scaleY(0);
	transition: all 0.3s ease;
}

.tab-content .tab-content__wrapp {
	background: #f5f4f4;
	padding: 35px 38px 30px 30px;
	border-radius: 10px;
	box-shadow: 0 0 20px 1px rgba(0, 0, 0, 0.15);
}

.tab-content p {
	line-height: 1.5;
	text-align: justify;
}

.tab-content.open {
	height: auto;
	opacity: 1;
	z-index: 1;
	transform: scaleY(1);
}

/*=== begin product tab ===*/
.block-path {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	padding: 0 0 63px 260px;
	margin-top: -31px;
}

.block-path::before {
	background: url("../img/bg/path_left.jpg") no-repeat;
	position: absolute;
	display: block;
	content: "";
	width: 352px;
	height: 288px;
	left: -120px;
	bottom: -25px;
	z-index: 0;
}

.block-path .block-path__article, .block-path .block-path__aside {
	flex-basis: 0;
	flex-grow: 1;
}

.block-path .block-path__article {
	position: relative;
	padding-right: 60px;
}

.block-path .block-path__article p {
	text-align: justify;
}

.block-path .title {
	margin-bottom: 32px;
}

.block-path .aside--path {
	flex: 0 0 155px;
	max-width: 155px;
	margin-top: 20px;
}

.aside .aside__title {
	font-weight: 700;
	font-size: 1.6rem;
	color: #be2c37;
	text-transform: uppercase;
	display: inline-block;
	line-height: 1.45;
}

.aside .aside__nav {
	margin-top: 20px;
}

.aside .aside__nav li {
	margin-bottom: 9px;
}

.aside .aside__nav a {
	font-size: 1.3rem;
	color: #000;
	position: relative;
	display: inline-block;
	line-height: 1.2;
	transition: background 0.3s ease-in-out,border-color 0.3s ease-in-out,color 0.3s ease-in-out;
}

.aside .aside__nav a::after {
	background: #be2c37;
	position: absolute;
	display: block;
	content: "";
	width: 100%;
	height: 1px;
	bottom: 0;
	left: 0;
	transform: scaleX(0);
	transform-origin: left center;
	transition: all 0.3s ease;
}

.aside .aside__nav a:hover {
	color: #be2c37;
}

.aside .aside__nav a:hover::after {
	transform: scaleX(1);
}

.text-cite {
	background: #b1181c;
	font-weight: normal;
	font-size: 3rem;
	font-family: 'Playfair Display Italic', serif;
	color: #fff;
	text-align: center;
	position: relative;
	line-height: 36px;
	padding: 35px 15px;
	z-index: 1;
}

.text-cite br {
	display: none;
}

@media (max-width: 1190px) {
	.section-history {
		background: url(../img/bg/tab-bg.jpg) no-repeat right 25%;
	}
	.product-tab {
		background-image: none;
	}
	.text-cite {
		margin-left: -15px;
		margin-right: -15px;
	}
}

@media (max-width: 991px) {
	.block-path {
		padding-left: 150px;
	}
	.block-path::before {
		left: -220px;
	}
	.block-path .title {
		font-size: 3rem;
	}
	.block-path .aside--path {
		align-self: flex-end;
	}
}

@media (max-width: 767px) {
	.section-history {
		margin-top: 30px;
	}
	.block-path {
		padding: 0;
		margin-top: 0;
	}
	.block-path::before {
		display: none;
	}
	.block-path .title {
		font-size: 2.4rem;
	}
	.product-tab {
		min-height: 300px;
	}
	.tabs {
		flex: 0 0 180px;
		max-width: 180px;
		padding-right: 10px;
	}
	.tabs li {
		margin-bottom: 1px;
	}
	.tabs li a {
		padding-left: 20px;
		line-height: 1.15;
	}
	.tab-content .tab-content__wrapp {
		padding: 18px 20px 25px 23px;
	}
	.tab-content p {
		margin-bottom: 15px;
	}
	.text-cite {
		font-size: 2.4rem;
		padding: 16px 15px 17px 15px;
		margin-top: 22px;
	}
	.text-cite br {
		display: block;
	}
}

@media (max-width: 660px) {
	.block-path .block-path__article {
		padding-right: 22px;
	}
}

@media (max-width: 600px) {
	.section-history {
		background: url(../img/bg/tab-bg_2.jpg) no-repeat right 29%;
	}
}

@media (max-width: 540px) {
	.product-tab {
		display: block;
	}
	.tabs {
		max-width: 250px;
		padding-right: 0;
		margin: 0 0 30px 0;
	}
	.tabs li {
		margin-bottom: 14px;
	}
	.block-path {
		display: block;
	}
	.block-path .aside--path {
		max-width: 100%;
	}
	.aside .aside__nav {
		display: flex;
		flex-wrap: wrap;
		margin: 0 -15px;
	}
	.aside .aside__nav li {
		flex: 0 50%;
		max-width: 50%;
		padding: 0 15px;
	}
}

/*== end section history ==*/
/*== begin form ==*/
/* ! == FORM ==*/
.box-form {
	position: relative;
}

.box-form .box-form__title {
	font-weight: 500;
	font-size: 2.2rem;
	color: #fff;
	display: block;
	text-align: center;
	margin-bottom: 30px;
}

.form-group.row {
	margin: 0 -6px;
}

.form-group.row .block-input.col {
	padding: 0 6px;
	margin-bottom: 10px;
}

.form .form__block {
	position: relative;
	width: 100%;
	margin-bottom: 15px;
}

.form .form__block input, .form .form__block textarea {
	background: transparent;
	font-weight: 400;
	font-size: 1.4rem;
	font-family: 'Inter', sans-serif;
	color: #000;
	display: block;
	width: 100%;
	padding: 0 10px 0 15px;
	transition: background 0.3s ease-in-out,border-color 0.3s ease-in-out,color 0.3s ease-in-out;
	border-radius: 5px;
	border: 1px solid #333;
}

.form .form__block input:hover, .form .form__block input:focus, .form .form__block textarea:hover, .form .form__block textarea:focus {
	background: transparent;
}

.form .form__block input {
	height: 35px;
	line-height: 34px;
}

.form .form__block textarea {
	text-align: left;
	min-height: 120px;
	max-height: 120px;
	min-width: 100%;
	max-width: 100%;
	padding: 14px 10px 10px 15px;
}

.form .form__button {
	display: flex;
	justify-content: center;
	margin-top: 30px;
}

